import "setimmediate";
import "array.polyfill";
import "es6-promise/auto"; // for webpack promise fixed

import React from "react";
import ReactDOM from "react-dom";
import initWorker from "reshow-worker";
import { ajaxDispatch } from "organism-react-ajax";
import { urlStore } from "reshow-url";
import { win, doc } from "win-doc";
import build from "reshow-build";

var render = function render(oApp, dom) {
  return (dom.innerHTML && ReactDOM.hydrate ? ReactDOM.hydrate : ReactDOM.render)(oApp, dom);
};

var update = function update(json) {
  return ajaxDispatch("callback", {
    json: json
  });
};

var bInitWorker = false;

var client = function client(rawApp, selector) {
  var app = build(rawApp);
  setImmediate(function () {
    win().Reshow = {
      render: render,
      app: app,
      update: update
    };
    var data = {};

    if ("undefined" !== typeof REACT_DATA) {
      data = REACT_DATA;
    }

    var appSelector = selector || "#app";
    var attachDom = doc().querySelector(appSelector);

    if (attachDom) {
      render(app(data), attachDom);
    }

    if (!bInitWorker) {
      initWorker();
      bInitWorker = true;
    }
  });
};

export default client;
export { render };