import { inject, create, remove } from "create-el";
import getRandomId from "get-random-id";
import { win } from "win-doc";

var isBlob = function isBlob(o) {
  return o instanceof Blob || Object.prototype.toString.call(o) === "[object Blob]";
};

var execDownload = function execDownload(text, fileName, option, isKeep) {
  var URL = win().URL;
  var blob = isBlob(text) ? text : new Blob([text], option);
  var url = URL.createObjectURL(blob);
  var link = create("a")()({
    download: fileName || getRandomId(),
    href: url
  });
  inject()(link);
  link.click();

  if (!isKeep) {
    remove(link);
    URL.revokeObjectURL(url);
  }
};

export default execDownload;